.social-block {
  position: relative;

  &:hover {
    .social-block__list {
      opacity: 1;
      pointer-events: auto;
      transition: opacity 0.5s;
    }
  }
}

.social-block__toggler {
  @include btn-reset;
  position: relative;
  display: block;
  width: 4rem;
  height: 4rem;
  color: $c-dark;
  border-radius: 50%;
  transition: color 0.5s;
  opacity: 0;
  transform: scale(0.5);
  transition: opacity 0.3s, transform 0.3s;
  transition-delay: 0.2s;
  transition-timing-function: cubic-bezier(0.35, 0.75, 0.6, 1.93);

  body.loaded & {
    opacity: 1;
    transform: scale(1);
  }

  body.dark-blue & {
    color: $c-light-blue;
  }

  body.light-blue & {
    color: $c-dark-blue;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    display: none;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    display: none;
  }

  &::before {
    @include center;
    content: "";
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: $c-light;
    border: 2px solid $c-light;
    z-index: -1;
    transition: width 0.5s, height 0.5s, background-color 0.5s, border-color 0.5s;
  }

  svg {
    @include center;
    display: block;
    width: 2.3rem;
    height: 2.2rem;
  }

  &:hover,
  .social-block--active & {
    color: $c-purple;
    transition: color 0.5s;

    &::before {
      width: 112.5%;
      height: 112.5%;
      transition: all 0.5s;
    }
  }

  &:focus {
    color: $c-purple;
    outline: none;
    transition: color 0.5s;

    body.dark-blue & {
      color: $c-light-blue;
    }
  
    body.light-blue & {
      color: $c-dark-blue;
    }

    &::before {
      background-color: $c-light;
      border-color: $c-purple;
      transition: all 0.5s;

      body.dark-blue & {
        border-color: $c-light-blue;
      }
    
      body.light-blue & {
        border-color: $c-dark-blue;
      }
    }

    & + .social-block__list {
      opacity: 1;
      pointer-events: auto;

      .social-block__link {
        opacity: 1;
        transform: translateY(-5px);
  
        &--fb {
          transition: all 0.5s ease;
        }
  
        &--insta {
          transition: all 0.5s ease 0.1s;
        }
  
        &--vk {
          transition: all 0.5s ease 0.2s;
        }
      }
    }
  }

  &:active,
  &:active:focus {
    color: $c-light;
    transition: color 0.5s;

    body.dark-blue & {
      color: $c-light;
    }
  
    body.light-blue & {
      color: $c-light;
    }

    &::before {
      width: 100%;
      height: 100%;
      background-color: $c-purple;
      border-color: $c-purple;
      transition: all 0.2s;

      body.dark-blue & {
        background-color: $c-light-blue;
        border-color: $c-light-blue;
      }
    
      body.light-blue & {
        background-color: $c-dark-blue;
        border-color: $c-dark-blue;
      }
    }
  }
}

.social-block--active {
  opacity: 1;
  pointer-events: auto;

  .social-block__link {
    opacity: 1;
    transform: translateY(-5px);

    &--fb {
      transition: all 0.5s ease;
    }

    &--insta {
      transition: all 0.5s ease 0.1s;
    }

    &--vk {
      transition: all 0.5s ease 0.2s;
    }
  }
}

.social-block__list {
  @include list-reset;
  position: absolute;
  top: 100%;
  right: 0;
  margin-top: 1.5rem;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.5s;

  @media (min-width: $stop-scaling) {
    margin-top: 15px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    position: static;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 0;
    opacity: 1;
    pointer-events: auto;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    position: static;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 0;
    opacity: 1;
    pointer-events: auto;
  }

  &::before {
    content: "";
    position: absolute;
    bottom: 100%;
    left: 0;
    right: 0;
    height: 1.5rem;

    @media (min-width: $stop-scaling) {
      height: 15px;
    }
  }

  &:hover {
    opacity: 1;
    pointer-events: auto;
  }

  &:focus-within {
    opacity: 1;
    pointer-events: auto;
    transition: opacity 0.5s;

    .social-block__link {
      opacity: 1;
      pointer-events: auto;
      transform: translateY(-5px);
    }
  }

  li {
    flex: none;
    margin-bottom: 0.5rem;

    @media (min-width: $stop-scaling) {
      margin-bottom: 5px;
    }

    @media (max-width: $tablet) and (orientation: portrait) {
      margin: 0 1.25rem;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      margin: 0 1.25rem;
    }
  }
}

.social-block__link {
  position: relative;
  display: block;
  width: 4rem;
  height: 4rem;
  color: $c-light;
  border: 2px solid transparent;
  border-radius: 50%;
  opacity: 0;
  transform: translateY(0px);
  transition: color 0.5s, opacity 0.5s, transform 0.5s;

  @media (min-width: $stop-scaling) {
    width: 40px;
    height: 40px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    color: $c-light;
    background-color: $c-dark;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    color: $c-light;
    background-color: $c-dark;
  }

  &:hover {
    color: $c-purple;
    transition: color 0.5s;

    body.dark-blue & {
      color: $c-light-blue;
    }
  
    body.light-blue & {
      color: $c-dark-blue;
    }
  }

  &:focus {
    color: $c-purple;
    background-color: $c-light;
    border-color: $c-purple;
    outline: none;
    transition: all 0.5s;

    body.dark-blue & {
      color: $c-light-blue;
      border-color: $c-light-blue;
    }
  
    body.light-blue & {
      color: $c-dark-blue;
      border-color: $c-dark-blue;
    }
  }

  &:active,
  &:active:focus {
    color: $c-purple;
    background-color: $c-light;
    border-color: $c-light;
    transition: all 0.5s;
  }

  svg {
    @include center;
  }

  &--fb {
    svg {
      width: 0.8rem;
      height: 1.7rem;

      @media (min-width: $stop-scaling) {
        width: 8px;
        height: 17px;
      }
    }
  }

  &--insta {
    svg {
      width: 1.5rem;
      height: 1.5rem;

      @media (min-width: $stop-scaling) {
        width: 15px;
        height: 15px;
      }
    }
  }

  &--vk {
    svg {
      width: 1.7rem;
      height: 1rem;

      @media (min-width: $stop-scaling) {
        width: 10px;
        height: 10px;
      }
    }
  }
}
